<template>
  <div>
    <!-- 帅选条件 -->
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->
        <el-form-item label="产品编码">
          <el-input v-model="listQuery.finished_product_code"></el-input>
        </el-form-item>

        <el-form-item label="处理结果">
          <el-select v-model="listQuery.solve_method" placeholder="请选择">
            <el-option label="全部" value=""></el-option>
            <el-option label="返修" value="返修"></el-option>
            <el-option label="报废" value="报废"></el-option>
          </el-select>
        </el-form-item>

        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查询</el-button
          >
          <el-button size="medium" @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 表格 -->
    <el-card class="box-card mt20">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span>不良品</span>
      </div> -->
      <div class="">
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="ispagination"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>

    <!-- 不良产品 弹出层 -->
    <el-dialog
      :title="dialogTitle"
      width="600px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="100px"
      >
        <el-form-item label="产品编码" prop="finished_product_code">
          <el-input v-model="dialogForm.finished_product_code"></el-input>
        </el-form-item>
        <el-form-item label="不良原因" prop="description">
          <el-input
            type="textarea"
            v-model="dialogForm.description"
          ></el-input>
        </el-form-item>

        <el-form-item label="处理方式" prop="solve_method">
          <el-select
            v-model="dialogForm.solve_method"
            placeholder="请选择"
            style="width: 100%"
          >
            <el-option label="返修" value="返修"></el-option>
            <el-option label="报废" value="报废"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="返回工站" prop="leader_work_id">
          <el-select
            v-model="dialogForm.leader_work_id"
            placeholder="请选择"
            style="width: 100%"
          >
            <el-option
              v-for="(item, index) in WorkCodeNameList"
              :key="index + ' dialog'"
              :label="item.work_name"
              :value="item.work_code"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="备注" prop="solve_result">
          <el-input
            type="textarea"
            v-model="dialogForm.solve_result"
          ></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

export default {
  name: "BadProduct",
  components: {
    ELTable,
  },
  data() {
    return {
      // 筛选
      listQuery: {
        product_plan_code: "",
        matter_code: "",
        solve_method: "",
        finished_product_code: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        product_plan_code: "",
        matter_code: "",
        solve_method: "",
        finished_product_code: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      ispagination: true, // 分页是否显示
      // 不良产品弹出层
      dialogTitle: "",
      dialogFlag: false,

      dialogForm: {
        
        unqualified_product_code: "",
        product_plan_code: "",
        finished_product_code: "",
        matter_code: "",
        description: "",
        leader_work_id: "",
        later_work_id: "",
        solve_method: "",
        solve_result: "",
      },
      dialogFormBase: {
        unqualified_product_code: "",
        product_plan_code: "",
        finished_product_code: "",
        matter_code: "",
        description: "",
        leader_work_id: "",
        later_work_id: "",
        solve_method: "",
        solve_result: "",
      },
      dialogFormRules: {
        product_plan_code: [
          { required: true, message: "请选择处理方式", trigger: "change" },
        ],
        product_plan_code: [
          { required: true, message: "请选择处理前工站", trigger: "change" },
        ],
      },
      // 表格列
      tableCol: [
        {
          label: "产品编码",
          field: "finished_product_code",
          align: "center",
        },
        // {
        //   label: "物料编码",
        //   field: "matter_code",
        //   align: "center",
        // },
        // {
        //   label: "物料名称",
        //   field: "matter_name",
        //   align: "center",
        // },
        {
          label: "不良原因",
          field: "description",
          align: "center",
        },
        {
          label: "处理方式",
          field: "solve_method",
          align: "center",
        },
        {
          label: "备注",
          field: "solve_result",
          align: "center",
        },
        {
          label: "操作",
          colType: "solt",
          align: "center",
          option: [
            {
              label: "修改",
              event: this.colEditBtn,
            },
            {
              label: "删除",
              event: this.colDeleteBtn,
            },
          ],
        },
      ],
      tableData: [],
    };
  },

  computed: {
    ...mapState([
      "MatterCodeNameList",
      "WorkCodeNameList",
      "ProductPlanCodeNameList",
      "CurrentUserInfo",
    ]),
  },
  methods: {
    ...mapActions([
      "getLogisticMatterNameCode",
      "getLogisticWorkCodeName",
      "getLogisticProductPlanCodeName",
    ]),

    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticUnqualifiedProduct(this.listQuery);
    },
    // 重置
    resetForm() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticUnqualifiedProduct(this.listQuery);
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticUnqualifiedProduct(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticUnqualifiedProduct(this.listQuery);
    },

    // 不良品 查询
    getLogisticUnqualifiedProduct(params) {
      baseManagement.getLogisticUnqualifiedProduct(params).then((res) => {
        console.log("不良品 查询", res);
        if (res.code == 0) {
          let tableData = res.data.data;

          tableData.forEach((tableDataItem) => {
            this.ProductPlanCodeNameList.forEach((CodeNameListItem) => {
              if (
                tableDataItem.product_plan_code ==
                CodeNameListItem.product_plan_code
              ) {
                tableDataItem.product_plan_name =
                  CodeNameListItem.product_plan_name;
              }
            });

            this.MatterCodeNameList.forEach((CodeNameListItem) => {
              if (tableDataItem.matter_code == CodeNameListItem.matter_code) {
                tableDataItem.matter_name = CodeNameListItem.matter_name;
              }
            });
            // this.WorkCodeNameList.forEach((CodeNameListItem) => {
            //   if (tableDataItem.work_code == CodeNameListItem.work_code) {
            //     tableDataItem.work_name = CodeNameListItem.work_name;
            //   }
            // });
          });
          this.tableData = tableData;
          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 产品结构 修改
    colEditBtn(rowData) {
      console.log(rowData);
      this.dialogTitle = "修改不良产品";
      this.dialogFlag = true;
      this.productIdReadonly = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });

      this.dialogForm = {
        unqualified_product_code: rowData.unqualified_product_code,
        product_plan_code: rowData.product_plan_code,
        finished_product_code: rowData.finished_product_code,
        matter_code: rowData.matter_code,
        description: rowData.description,
        leader_work_id: rowData.leader_work_id,
        later_work_id: rowData.later_work_id,
        solve_method: rowData.solve_method,
        solve_result: rowData.solve_result,
      };
    }, 

    getLogisticPutUnqualifiedProduct(params) {
      baseManagement.getLogisticPutUnqualifiedProduct(params).then((res) => {
        console.log("不良产品 修改", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticUnqualifiedProduct(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 弹出层确认按钮
    dialogSave() {
      this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口

          if (this.dialogTitle == "修改不良产品") {
            console.log("修改不良产品", this.dialogForm);
            this.getLogisticPutUnqualifiedProduct(this.dialogForm);
          }
        }
      });
    },

    // 不良产品 删除
    getLogisticDeleteUnqualifiedProduct(params) {
      baseManagement.getLogisticDeleteUnqualifiedProduct(params).then((res) => {
        console.log("不良产品 删除", res);
        if (res.code == 0) {
          this.getLogisticUnqualifiedProduct(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    colDeleteBtn(rowData) {
      this.$confirm("确定执行删除操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.getLogisticDeleteUnqualifiedProduct({
          unqualified_product_code: rowData.unqualified_product_code,
        });
      });
    },
  },
  mounted() {
    
      // 物料内码和物料名称 查询
       this.getLogisticMatterNameCode();
      // 工站内码和工站名称接口： 查询
       this.getLogisticWorkCodeName();
      // 生产计划内码和名称接口 查询
       this.getLogisticProductPlanCodeName();
    // 不良品 查询
    this.getLogisticUnqualifiedProduct(this.listQuery);
  },
};
</script>

<style>
</style>